  async makePaymentRequest(amount, nonce) {
    const paymentDetails = {
      paymentAmount: amount,
      nonceFromTheClient: nonce
    }
    return await   this.http.post('https://app.lideart.net/checkoutWithPayment', paymentDetails).toPromise();
  }

